import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import anime from 'animejs/lib/anime.es.js';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  galleryId = 'homegal';
  testSlide = 0;
  testims: Testimonial[] = [
    { content: 'It\'s been a long journey! A journey spanning 19 long years in earnestly making a qualitative difference to the lives of tens of thousands of children and their aspirational parents.', person: 'William henry smith' },
    { content: 'A journey spanning 19 long years in earnestly making a qualitative difference to the lives of tens of thousands of children and their aspirational parents.', person: 'Henry' }
  ];
  items: any[] = [
    { thumb: '/assets/images/gall/1.png', src: '/assets/images/gall/1.png' },
    { thumb: '/assets/images/gall/2.png', src: '/assets/images/gall/2.png' },
    { thumb: '/assets/images/gall/3.png', src: '/assets/images/gall/3.png' },
    { thumb: '/assets/images/gall/4.png', src: '/assets/images/gall/4.png' },
    { thumb: '/assets/images/gall/5.png', src: '/assets/images/gall/5.png' },
    { thumb: '/assets/images/gall/6.png', src: '/assets/images/gall/6.png' },
    { thumb: '/assets/images/gall/7.png', src: '/assets/images/gall/7.png' },
    { thumb: '/assets/images/gall/8.png', src: '/assets/images/gall/8.png' }
  ];

  constructor() { }

  ngOnInit() {
  }

  prevSlide() {
    this.testSlide--;
    $('.testim').removeClass('active');
    anime({
      targets: $('.testim').get(this.testSlide),
      translateX: ['-30px', '0px'],
      opacity: [0, 1],
      duration: 700,
      begin: (an) => {
        $('.testim').eq(this.testSlide).addClass('active');
      }
    });
  }

  nextSlide() {
    this.testSlide++;
    $('.testim').removeClass('active');
    anime({
      targets: $('.testim').get(this.testSlide),
      translateX: ['30px', '0px'],
      opacity: [0, 1],
      duration: 1000,
      begin: (an) => {
        $('.testim').eq(this.testSlide).addClass('active');
      }
    });
  }
}

interface Testimonial {
  content: string;
  person: string;
}
