import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { AcademicsComponent } from './components/academics/academics.component';
import { AdmissionsComponent } from './components/admissions/admissions.component';
import { GalleryComponent } from './components/gallery/gallery.component';
import { ContactUsComponent } from './components/contact-us/contact-us.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import { PhilosophyComponent } from './components/philosophy/philosophy.component';
import { AboutUsComponent } from './components/about-us/about-us.component';


const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'know-us/about-us', component: AboutUsComponent},
  {path: 'know-us/philosophy', component: PhilosophyComponent},
  {path: 'academics', component: AcademicsComponent},
  {path: 'admissions', component: AdmissionsComponent},
  {path: 'gallery', component: GalleryComponent},
  {path: 'contact-us', component: ContactUsComponent},
  {path: '404', component: PageNotFoundComponent},
  {path: '**', redirectTo: '404'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
